<template>
  <div class="upload">
    <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-lx-calendar"></i> </el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
    </div>
    <div class="container">
      <div class="content-title"></div>
       <div class="plugins-tips">
                Element UI
                <a href="http://element.eleme.io/#/zh-CN/component/upload" target="_blank">Element UI Upload</a>
       </div>
      <el-upload
                class="upload-demo"
                drag
                action="http://jsonplaceholder.typicode.com/api/posts/"
                multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text"><em></em></div>
                <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
      </el-upload>
      <div class="content-title"></div>
        <div class="plugins-tips">
          vue-cropperjs cropperjs  Vue 
          <a href="https://github.com/Agontuk/vue-cropperjs" target="_blank">vue-cropperjs</a>
      </div>
      <div class="crop-demo">
        <img :src="cropImg" class="pre-img" alt="">
        <div class="crop-demo-btn">
          <input type="file" class="crop-input" name="image" accept="image/" @change="setImage">
        </div>
      </div>

      <el-dialog title="" :visible.sync="dialogVisible" width="30%">
        <vue-cropper ref="cropper" :src="imgSrc" :ready="cropImage" :zoom="cropImage" :cropmove="cropImage" style="width:100%;height:300px">
          <span slot="footer" class="dialog-footer"></span>
            <el-button @click="cancelCrop"> </el-button>
            <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </vue-cropper>
      </el-dialog>

    </div>
  </div>
</template>

<script>
   import VueCropper  from 'vue-cropperjs';
export default {
  name: 'upload',
  data () {
    return {
      dialogVisible: false,
      defaultSrc: require('../assets/img/img.jpg'),
      filleList: [],
      imgSrc: '',
      cropImg: ''
    }
  },
  components: {
    VueCropper
  },
  created () {
    this.cropImg = this.defaultSrc
  },
  methods: {
    setImage (e) {
      const file = e.target.files[0]
      if(!file.type.includes('image/')) {
        return
      }
      const reader = new FileReader()
      reader.onload = (event) => {
        this.dialogVisible = true
        this.imgSrc = event.target.result
        this.$refs.cropper && this.$refs.cropper.replace(event.target.result)
      }
      reader.readAsDataURL(file)
      console.log(reader);
    },
    cropImage () {
      this.cropImg = this.$refs.cropper.getCroppedCanvas().toDataURL()
    },
    cancelCrop () {
      this.dialogVisible = false
      this.cropImg = this.defaultSrc
    },
    imageuploaded (res) {
      console.log(res);
    },
    handleError () {
      this.$notify.error({
        title: '',
        message: ''
      })
    }
  }
}
</script>

<style scoped>
  .content-title {
    font-weight: 400;
    line-height: 50px;
    margin: 10px 0;
    font-size: 22px;
    color: #666;
  }
    .pre-img{   
        width: 100px;
        height: 100px;
        background: #f8f8f8;
        border: 1px solid #eee;
        border-radius: 5px;
    }
    .crop-demo{
        display: flex;
        align-items: flex-end;
    }
    .crop-demo-btn{
        position: relative;
        width: 100px;
        height: 40px;
        line-height: 40px;
        padding: 0 20px;
        margin-left: 30px;
        background-color: #409eff;
        color: #fff;
        font-size: 14px;
        border-radius: 4px;
        box-sizing: border-box;
    }
    .crop-input{
        position: absolute;
        width: 100px;
        height: 40px;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;
    }
</style>